* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --red: #fe4443;
  --grey: #f0efef;
  --grey2: #6e6e6e;
  --lightBlack: #464955;
  --black: #13222a;
  --vlack: #000;
  --white: #fff;
}

@font-face {
  font-family: "alagard";
  src: local("alagard"), url("/fonts/alagard.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}


body {
  font-family: "alagard", sans-serif;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  overscroll-behavior: none;

  box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: thin;
  scrollbar-color: var(--red);
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: var(--red);
  border-radius: 2px;
}

.rotation {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 11;
  background-color: var(--grey);
  font-family: "alagard", sans-serif;
  color: var(--red);
  display: none;
}

.rotation p {
  width: 80%;
}

.loader {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: var(--vlack);
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2em;
  font-size: 22px;
  font-family: "alagard", sans-serif;
}

.progress {
  position: absolute;
  top: 60%;
  width: 50%;
  height: 30px;
  background: var(--white);
  transform: scaleX(0);
  transition: transform 1s;
  transform-origin: left;
  z-index: 10;
  margin: 0 5%;
}

.nav--bar {
  display: flex;
  align-content: center;
  width: 100%;
  justify-content: center;
  font-family: "alagard", sans-serif;
  margin-bottom: 1%;
  margin-top: 20px;
  font-size: 3vw;
}

@media (max-width: 768px){
  
  .nav--bar{
      font-size: 2vh;
      
  }

}


main {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100vh;
  scroll-snap-type: mandatory;
  scroll-snap-points-y: repeat(100vh);
  scroll-snap-type: y mandatory;
}

.section {
  width: 100vw;
  height: 100vh;
  scroll-snap-align: center;
}

.first {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.section--one--container1 {
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;

  height: fit-content;
  /* margin-top: 8%; */
  position: relative;
}

.first h1 {
  font-size: 6vw;
  font-family: "alagard", sans-serif;
  color: var(--vlack);
 
}
@media (max-width: 768px){
  .first h1 {
    font-size: 6vw;
  }
  }


.first h1 span {
  color: var(--red);
}

.first p {
  font-size: 2vw;
  font-weight: 400;
  color: var(--vlack);
}

.first h3 {
  color: var(--grey);
  position: absolute;
  font-size: 9vw;
  white-space: nowrap;
  z-index: -1;
  text-transform: uppercase;
  top: 40%;
}

.section--one--container2 {
  width: 70%;
  margin-bottom: 6%;
  display: flex;
  justify-content: space-between;
}

.spec--wrapper .name {
  font-size: 1.5vw;
  font-weight: 400;
  color: var(--vlack);
}

.value--wrapper {
  display: flex;
  align-items: flex-end;
}

.value--wrapper .value {
  font-size: 2.3vw;
  font-weight: 200;
  font-family: "alagard", sans-serif;
  color: var(--red);
}
.value--wrapper .unit {
  font-size: 1vw;
  margin: 0 5px;
  margin-bottom: 0.5vw;
  font-weight: 600;
  color: var(--vlack);
}

.second {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  
}

.section--two--container1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: rotateZ(45deg) translateY(225px);
  transition: all 0.5s ease-in-out;
  opacity: 0;
}

.second h1 {
  font-size: 5vw;
  font-family: "alagard", sans-serif;
  color: var(--vlack);
  white-space: 0px;
}

.section--two--container2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 8%;
  transition: all 0.5s ease-in-out;
  transform: translateX(450px);
  opacity: 0;
}

.second span {
  font-size: 6vw;
  font-family: "alagard", sans-serif;
  color: var(--red);
  writing-mode: vertical-rl;
  text-orientation: upright;
  letter-spacing: 0px;
}

.second p {
  font-size: 2rem;
  font-family: "alagard", sans-serif;
  color: var(--vlack);
}

.leftSide {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
}

.section--three--container {
  display: flex;
  flex-direction: column;

  width: 30%;
  margin-left: 10%;
  transform: translateX(-1000px);
  transition: all 0.5s ease-in-out;
  opacity: 0;
}

.rightSide {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: auto;
}

.section--four--container {
  display: flex;
  flex-direction: column;

  width: 30%;
  margin-right: 10%;

  transform: translateX(1000px);
  transition: all 0.5s ease-in-out;
  opacity: 0;
}

.section--five--container {
  display: flex;
  flex-direction: column;

  width: 30%;
  margin-right: 10%;
  transform: translateX(1000px);
  transition: all 0.5s ease-in-out;
  opacity: 0;
}

.six {
  position: relative;
}

.section--six--container {
  display: flex;
  flex-direction: column;
  

  width: 30%;
  margin-left: 10%;
  transform: translateX(-1000px);
  transition: all 0.5s ease-in-out;
  opacity: 0;
}

.title {
  font-size: 5vw;
  line-height: 5vw;
  color: var(--red);
  white-space: 0px;
  margin-bottom: 20px;
  font-family: "alagard", sans-serif;
  text-transform: uppercase;
  top: 80%;
}

.titlesocial {
  font-size: 5vw;
  line-height: 5vw;
  color: var(--red);
  white-space: 0px;
  margin-bottom: 250px;
  font-family: "alagard", sans-serif;
  text-transform: uppercase;
}

.detail {
  font-size: 1.5vw;
  line-height: 2.2vw;
  color: var(--vlack);
  font-weight: 200;
}

footer {
  background: var(--vlack);
  padding: 30px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  bottom: 0;

  width: 100%;
}

footer > a {
  text-decoration: none;
  color: white;
}

.button--footer {
  background: white;
  color: black;
  border: none;
  border-radius: 50px;
  padding: 10px 30px;
  font-size: 0.6rem;
  cursor: pointer;
  transition: all 0.8s ease;
}

.button--footer:hover {
  background: black;
  color: white;
}

.resetPosition {
  transform: translateX(0px);
  transition: transform 0.5s ease-in-out, opacity 1s ease-in-out;
  opacity: 1;
}

.activeRightSpecific {
  transform: rotateZ(-90deg) translateY(0px);
  opacity: 1;
  transition: transform 0.5s ease-in-out, opacity 1s ease-in-out;
}

#webgi-canvas {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;

  pointer-events: none;
  z-index: -1;
}

ul {
  position:absolute;
  top:50%;
  left:50%;
  transform:translate(-50%, -50%);
  display:flex;
  margin:0;
  padding:0;
}

ul li {
  list-style:none;
  margin:0 5px;
}

ul li a .fab {
  font-size: 40px;
  color: #262626;
  line-height:80px;
  transition: .5s;
  padding-right: 14px;
}

ul li a span {
  padding:0;
  margin:0;
  position:absolute;
  top: 30px;
  color: #262626;
  letter-spacing: 4px;
  transition: .5s;
}

ul li a {
  text-decoration: none;
  display:absolute;
  display:block;
  width:210px;
  height:80px;
  background: #fff;
  text-align:left;
  padding-left: 20px;
  transform: rotate(-30deg) skew(25deg) translate(0,0);
  transition:.5s;
  box-shadow: -20px 20px 10px rgba(0,0,0,.5);
}
ul li a:before {
  content: '';
  position: absolute;
  top:10px;
  left:-20px;
  height:100%;
  width:20px;
  background: #b1b1b1;
  transform: .5s;
  transform: rotate(0deg) skewY(-45deg);
}
ul li a:after {
  content: '';
  position: absolute;
  bottom:-20px;
  left:-10px;
  height:20px;
  width:100%;
  background: #b1b1b1;
  transform: .5s;
  transform: rotate(0deg) skewX(-45deg);
}

ul li a:hover {
  transform: rotate(-30deg) skew(25deg) translate(20px,-15px);
  box-shadow: -50px 50px 50px rgba(0,0,0,.5);
}

ul li:hover .fa {
  color:#fff;
}

ul li:hover span {
  color:#fff;
}

ul li:hover:nth-child(1) a{
  background: #7289DA;
}
ul li:hover:nth-child(1) a:before{
  background: #7289DA;
}
ul li:hover:nth-child(1) a:after{
  background: #4a69ad;
}

ul li:hover:nth-child(2) a{
  background: #00aced;
}
ul li:hover:nth-child(2) a:before{
  background: #097aa5;
}
ul li:hover:nth-child(2) a:after{
  background: #53b9e0;
}

ul li:hover:nth-child(3) a{
  background: #dd4b39;
}
ul li:hover:nth-child(3) a:before{
  background: #b33a2b;
}
ul li:hover:nth-child(3) a:after{
  background: #e66a5a;
}

ul li:hover:nth-child(4) a{
  background: #e4405f;
}
ul li:hover:nth-child(4) a:before{
  background: #d81c3f;
}
ul li:hover:nth-child(4) a:after{
  background: #e46880;
}

.container {
  position: relative;
  width: 320px;
  margin: 100px auto 0 auto;
  perspective: 1000px;
}

.carousel {
  position: absolute;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d; 
  animation: rotate360 60s infinite forwards linear;
}
.carousel__face { 
  position: absolute;
  width: 300px;
  height: 187px;
  top: 20px;
  left: 10px;
  right: 10px;
  background-size: cover;
  box-shadow:inset 0 0 0 2000px rgba(0,0,0,0.5);
  display: flex;
}

@mixin white-gradient {
	background: linear-gradient(to right,  rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
}

$animationSpeed: 40s;

// Animation
@keyframes scroll {
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-250px * 7))}
}


// Styling
.slider {
	background: white;
	box-shadow: 0 10px 20px -5px rgba(0, 0, 0, .125);
	height: 100px;
	margin: auto;
	overflow:hidden;
	position: relative;
	width: 960px;
  padding: 10%;
	
	&::before,
	&::after {
		@include white-gradient;
		content: "";
		height: 100px;
		position: absolute;
		width: 200px;
		z-index: 2;
	}
	
	&::after {
		right: 0;
		top: 0;
		transform: rotateZ(180deg);
	}

	&::before {
		left: 0;
		top: 0;
	}
	
	.slide-track {
		animation: scroll $animationSpeed linear infinite;
		display: flex;
		width: calc(250px * 14);
	}
	
	.slide {
		height: 150px;
		width: 550px;
	}
}